@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.login-page {
    width: 100vw;
    height: 100vh;
    background-color: #F4F4F4;
    display: flex;
    justify-content: center;
}

.login-container-image{
    width: 50%;  
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-container-image img{
    width: 440px;
    height:439px;  
}

.login-container-form{
    width: 50%; 
    display:flex;
    justify-content: flex-start;
    align-items: center;

}

.login-container-form-square{
    width: 500px;
    height:500px;
    background-color: white;
    border-radius: 8px;
    font-family: 'Poppins';
}
.login-container-form-square a{
    font-weight: 500;
    font-size:25px;
    margin-left: 20px;
}

#title-bold {
    color: #6358DC;
    font-weight: bold;
    font-size:40px;
}

.login-button{
    width: 80%;
    height: 60px;
    background-color: white;
    border-radius: 6px;
    box-shadow: 0 0 5px rgba(0,0,0,0.3);
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.button-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 150px;
    align-items: center;
    margin-top: 40px;
    border: none;
    outline: none;
}
.button-container img{
    margin-right: 8px;
}

.button-container :hover{
    box-shadow: 0 0 5px rgba(0,0,0,0.6);
    cursor: pointer;
    border: none;
}

#separator-login{
    width: 80%;
    margin-top: 40px;
    border-color:#e7e7e7;
}

.description-container {
    width: 100%;
    text-align: center;
    margin-top: 40px;
    color: #6d6d6d;
}
.navbar{
    width: 100vw;
    height: 50px;
    background-color: #ECECEC;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.navbar-button {
    width: 150px;
    height: 40px;
    background-color: #6358DC;
    border-radius: 6px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar-button:hover {
    background-color: #6358dcb6;
    cursor: pointer;  
}

.navbar-image img {
    width: 40px;
    height: 40px;
    border-radius: 100px;
}

.navbar-image{
    width: 200px;
    height: 60px;
    display:flex;
    align-items: center;
    justify-content:space-around;
    font-weight: 300;
}

.chat-container{
    width: 900px;
    height: 400px;
    scroll-behavior: smooth;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}


.chat-container-message {
    width: 300px;
    height: 100px;
    background-color: #6358dcb6;
    border-radius: 10px;
    font-weight: 400;
    padding-left: 10px;
    color: white;
    display: flex;
    margin-bottom: 10px;
    align-items: center;
}

.chat-container-img img{
    width: 60px;
    height: 60px;
    overflow: hidden;
    border-radius: 100px;
}

.input-container{
    position: absolute;
    bottom: 0;
    width: 60%;
    margin-left: 20px;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
}

.input-container input{
    width: 350px;
    height: 40px;
}

.input-container button{
    height: 40px;
    margin-left: 40px;
    width: 150px;
    height: 40px;
    background-color: #6358DC;
    border-radius: 6px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    outline: none;
}

.input-container button:hover {
    background-color: #6358dcb6;
    cursor: pointer;  
}


.input-message-container {
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;    
}

.input-message-container input{
    height: 40px;
    border: none;
    width: 320px;
    border-color: aqua;
}

.input-message-container button{
    height: 40px;
    margin-left: 40px;
    width: 150px;
    height: 40px;
    background-color: #6358DC;
    border-radius: 6px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    outline: none;
}

.input-message-container button:hover{
        background-color: #6358dcb6;
        cursor: pointer;  
}

.container-chats {
    margin-top: 30px;
}
#contenedor{
    height: 500px;
    width: 30%;
    scroll-behavior: smooth;
    overflow-y: scroll;
}

.question-item {
    width: 300px;
    height: 90px;
    background-color: #6358DC;
    margin-left: 10px;
    border-radius:20px;
    display: flex;
    justify-content:center;
    align-items:center;
    font-weight: 400;
    color: white;
    margin-bottom: 10px;
}

.question-item:hover {
    background-color: #6358dcb6;
    cursor: pointer;
}

.question-item-image {
    width: 60px;
    height: 60px;
    border-radius: 200px;
    margin-left: 10px;
}

.question-item img{
    width:30px;
    height:30px;
}
.question-item-image img{
    width: 50px;
    height: 50px;
    border-radius:70px; 
}
#qustionary1{
    height: 40px;
}
#contener{
    display: flex;
}
